<template>
  <div class="container">
    <Spinner :class="{ 'fade-out': !isLoading }" />
    <BannerSlider />
    <div class="row mt-4">
      <ProductCards
        v-for="item in products"
        :key="item.id"
        :initial-item="item"
      />
    </div>
    <div class="row justify-content-center">
      <Pagination
        :currentPage="currentPage"
        :totalPage="totalPage"
        :prevPage="prevPage"
        :nextPage="nextPage"
        :categoryId="categoryId"
      />
    </div>
  </div>
</template>

<script>
import ProductCards from '../components/ProductCards'
import Pagination from '../components/Pagination'
import indexAPI from '../apis/products'
import BannerSlider from '../components/BannerSlider'
import Spinner from '../components/Spinner'
import { Toast } from '../utils/helpers'

export default {
  name: 'index',
  components: {
    BannerSlider,
    ProductCards,
    Pagination,
    Spinner,
  },
  data() {
    return {
      products: [],
      categories: [],
      categoryId: -1,
      currentPage: 1,
      totalPage: [],
      prevPage: -1,
      nextPage: -1,
      isLoading: false
    }
  },
  methods: {
    async fetchData({ queryPage, queryCategoryId }) {
      try {
        this.isLoading = true
        const res = await indexAPI.getIndex({
          page: queryPage,
          categoryId: queryCategoryId,
        })
        const {
          products,
          categories,
          categoryId,
          page,
          totalPage,
          prev,
          next,
        } = res.data
        this.products = products
        this.categories = categories
        this.categoryId = categoryId
        this.currentPage = page
        this.totalPage = totalPage
        this.nextPage = next
        this.prevPage = prev
        this.isLoading = false
      } catch (err) {
        console.log(err)
        this.isLoading = false
        this.toastFire('error', '無法取得首頁資訊')
      }
    },
    toastFire(action, text) {
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false,
      })
    },
  },
  beforeRouteUpdate(to, from, next) {
    const { page = '', categoryId = '' } = to.query
    this.fetchData({ queryPage: page, queryCategoryId: categoryId })
    next()
  },
  created() {
    this.fetchData({
      queryPage: '',
      queryCategoryId: '',
    })
  },
}
</script>

<style>
.carousel-3d-container {
  margin: 0 0 5rem 0 !important;
}
.carousel-3d-controls .next span,
.carousel-3d-controls .prev span {
  color: #fffffff1;
  font-size: 4rem;
  text-shadow: 4px 4px 2px #00000060;
}
.carousel-3d-slide {
  box-shadow: 0px 0px 35px #0000005d;
}
</style>
