import { apiHelper } from '../utils/helpers'

export default {
  getTopUsers () {
    return apiHelper.get('/admin/topUsers')
  },
  getCurrentUser () {
    return apiHelper.get('/admin/currentUser')
  },
  getUsers () {
    return apiHelper.get('/admin/users')
  },
  getUser ({ id }) {
    return apiHelper.get(`/admin/users/${id}`)
  },
  editUser ({ id, formData }) {
    return apiHelper.put(`/admin/users/${id}`, formData)
  },
  addLike ({ id }) {
    return apiHelper.post(`/admin/like/${id}`, null)
  },
  removeLike ({ id }) {
    return apiHelper.delete(`/admin/like/${id}`)
  },
  addFollowing ({ id }) {
    return apiHelper.post(`/admin/following/${id}`, null)
  },
  removeFollowing ({ id }) {
    return apiHelper.delete(`/admin/following/${id}`)
  }
}
