<template>
  <nav class="navbar navbar-expand-lg fixed-top navbar-dark bg-dark">
    <link v-if="isDark" rel="stylesheet" href="stylesheets/bootstrap-darkly.min.css" id="dark-theme-style">
    <router-link class="navbar-brand" to="/">
      <img class="img__box site__logo" src="prism_logo.svg" />
    </router-link>

    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarSupportedContent"
      aria-controls="navbarSupportedContent"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon" />
    </button>

    <div id="navbarSupportedContent" class="navbar-collapse collapse">
      <ul class="nav nav-pills ml-2">
        <li class="nav-item dropdown">
          <a
            class="nav-link dropdown-toggle pr-4 pl-4 text-white"
            data-toggle="dropdown"
            href="#"
            role="button"
            aria-haspopup="true"
            aria-expanded="false"
          >
            社群
          </a>
          <div class="dropdown-menu">
            <router-link name="feeds" class="dropdown-item" to="/feeds">
              最新動態
            </router-link>
            <router-link class="dropdown-item" to="/tops">
              人氣商品
            </router-link>
            <router-link class="dropdown-item" to="/tops/users">
              活躍用戶
            </router-link>
          </div>
        </li>
      </ul>

      <ul class="nav nav-pills ml-2">
        <li class="nav-item dropdown">
          <div
            class="nav-link dropdown-toggle pr-4 pl-4 text-white"
            data-toggle="dropdown"
            role="button"
            aria-haspopup="true"
            aria-expanded="false"
          >
            所有分類
          </div>

          <div class="dropdown-menu">
            <router-link class="dropdown-item" to="/results">
              全部
            </router-link>

            <router-link
              class="dropdown-item"
              v-for="category in categories"
              :key="category.id"
              :to="{
                name: 'results',
                query: { categoryId: category.id },
              }"
            >
              {{ category.name }}
            </router-link>
          </div>
        </li>
      </ul>

      <div
        class="navbar-collapse offcanvas-collapse"
        id="navbarsExampleDefault"
      >
        <ul class="navbar-nav mr-auto"></ul>

        <ul v-show="isLoading" v-if="isAuthenticated" class="nav nav-pills">
          <li class="nav-item dropdown">
            <div
              class="nav-link dropdown-toggle text-white"
              data-toggle="dropdown"
              href="#"
              role="button"
              aria-haspopup="true"
              aria-expanded="false"
            >
              <img
                v-if="currentUser.avatar"
                class="img__box mxw__28 rounded-circle mr-2 border border-light lazy__show"
                :src="currentUser.avatar"
              />

              <i v-else class="fas fa-user-circle font__size__nav mr-2"></i>

              {{ currentUser.name || 'user' }}
            </div>
            <div class="dropdown-menu">
              <router-link
                class="dropdown-item"
                :to="{
                  name: 'profile',
                  params: {
                    id: currentUser.id,
                  },
                }"
              >
                <i class="fas fa-id-badge mr-1"></i>
                會員資訊
              </router-link>
              <div class="dropdown-divider"></div>

              <router-link
                v-if="currentUser.isAdmin"
                class="dropdown-item"
                to="/admin"
              >
                <i class="fas fa-cog"></i>
                網站管理
              </router-link>

              <button class="dropdown-item" @click.prevent.stop="logout">
                <i class="fas fa-sign-out-alt mr-1"></i>
                登出
              </button>
            </div>
          </li>
        </ul>

        <div v-show="isLoading" v-else>
          <router-link to="/signin">
            <button
              class="btn btn-outline-warning pr-3 pl-3 btn-sm my-sm-0 rounded-pill"
            >
              登入
            </button>
          </router-link>
          <span class="text-light mr-1 ml-1">或</span>
          <router-link to="/signup">
            <button
              class="btn btn-outline-success pr-3 pl-3 btn-sm my-sm-0 rounded-pill"
            >
              註冊
            </button>
          </router-link>
        </div>

        <div class="custom-control custom-switch ml-3 mr-3 text-white">
          <input
            type="checkbox"

            :disabled="isProcessing"
            class="custom-control-input"
            @click="darkThemeSwitch"
            id="DarkSwitcher"
            :checked="isDark"
          />
          <label class="custom-control-label dark__switcher" for="DarkSwitcher">
            Dark
          </label>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { mapState } from 'vuex'
import { iziToast } from '../utils/helpers'
export default {
  computed: {
    ...mapState([
      'currentUser',
      'categories',
      'isAuthenticated',
      'isLoading',
      'isDark',
    ]),
  },
  data() {
    return {
      isProcessing: false,
    }
  },
  methods: {
    darkThemeSwitch() {
      const darkState = window.localStorage.getItem('isDark')
      if (darkState === 'isDark') {
        window.localStorage.setItem('isDark', 'isLight')
        this.$store.commit('toggleGlobalTheme', false)
      }
      if (darkState === 'isLight') {
        window.localStorage.setItem('isDark', 'isDark')
        this.$store.commit('toggleGlobalTheme', true)
      }
    },
    logout() {
      this.$store.commit('revokeAuthentication')
      this.$router.push('/signin')
      this.$toast.show(' ','成功登出.', iziToast.options.success)
    },
  },
  created() {
      // 判斷畚箕資訊
      const darkState = window.localStorage.getItem('isDark')
      if(!darkState) {
        window.localStorage.setItem('isDark', 'isDark')
        return this.$store.commit('toggleGlobalTheme', true)
      }
      if (darkState === 'isDark') {
        this.$store.commit('toggleGlobalTheme', true)
      }
      if (darkState === 'isLight') {
        this.$store.commit('toggleGlobalTheme', false)
      }

    // <link rel="stylesheet" href="/stylesheets/bootstrap-darkly.min.css" id="dark-theme-style">
  },
}
</script>
