<template>
  <div class="col-lg-3">
    <div class="card borderless shadow mb-4 p-4">
      <div class="card-head">
        <router-link
          :to="{
            name: 'products',
            params: {
              id: product.id,
            },
          }"
        >
          <img class="card-img-top lazy" :src="product.image" />
        </router-link>

        <div
          v-if="product.isLiked"
          class="like__block"
          :class="{ isActive: product.isLiked }"
        >
          <button class="btn text-danger"
            @click.prevent.stop="removeLiked(product.id)"
            :disabled="isProcessing"
          >
            <i class="fas fa-heart"></i>
          </button>
        </div>

        <div v-else class="like__block">
          <button
            class="btn text-light" 
            @click.prevent.stop="addLiked(product.id)"
            :disabled="isProcessing"
          >
            <i class="far fa-heart"></i>
          </button>
        </div>
      </div>

      <div class="card-body pt-3 p-1">
        <p class="card-text">
          <router-link
            :to="{
              name: 'products',
              params: {
                id: product.id,
              },
            }"
          >
            {{ product.name }}
          </router-link>
        </p>
        <span class="badge badge-pill badge-primary">
          {{ product.Category ? product.Category.name : '未分類' }}
        </span>
        <p class="card-text mt-2">{{ product.description }}</p>

        <div class="row pr-3 pl-3">
          <span>NT: {{ product.price }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import usersAPI from '../apis/users'
import { Toast } from '../utils/helpers'

import { fromNowFilter } from '../utils/mixins'
export default {
  name: 'ProductCards',
  props: {
    initialItem: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      product: this.initialItem,
      isProcessing: false
    }
  },
  methods: {
    async addLiked(id) {
      try {
        this.isProcessing = true
        const { data } = await usersAPI.addLike({ id })
        if (data.status !== 'success') {
          this.toastFire('error', data.message)
          throw new Error(data.message)
        }
        this.product = {
          ...this.product,
          isLiked: true,
        }
        this.toastFire('success', data.message)
        this.isProcessing = false
      } catch (err) {
        console.log(err)
        this.toastFire('error', '取得使用者資訊失敗')
      }
    },
    async removeLiked(id) {
      try {
        this.isProcessing = true
        const { data } = await usersAPI.removeLike({ id })
        if (data.status !== 'success') {
          this.toastFire('error', data.message)
          throw new Error(data.message)
        }
        this.product = {
          ...this.product,
          isLiked: false,
        }
        this.toastFire('success', data.message)
        this.isProcessing = false
      } catch (err) {
        console.log(err)
        this.toastFire('error', '取得使用者資訊失敗')
      }
    },
    toastFire(action, text) {
      Toast.fire({
        icon: action,
        title: text,
        background: '#eee',
        showConfirmButton: false,
      })
    },
  },
  mixins: [fromNowFilter],
}
</script>
