import moment from '.../../moment'

export const fromNowFilter = {
  filters: {
    fromNow (date) {
      if (!date) return '-'
      return `${moment(date).fromNow()}`
    }
  }
}

export const emptyImageFilter = {
  filters: {
    emptyImage (src) {
      return src || '/images/no_img.png'
    }
  }
}
