import Vue from 'vue'
import VueRouter from 'vue-router'
import NotFound from '../views/NotFound.vue'
import SignIn from '../views/SignIn.vue'
import Index from '../views/Index.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index
  },
  {
    path: '/signin',
    name: 'sign-in',
    component: SignIn
  },
  {
    path: '/signup',
    name: 'sign-up',
    component: () => import('../views/SignUp.vue')
  },
  {
    path: '/index',
    name: 'index',
    redirect: '/'
  },
  {
    path: '/results',
    name: 'results',
    component: () => import('../views/Results.vue')
  },
  {
    path: '/feeds',
    name: 'feeds',
    component: () => import('../views/Feeds.vue')
  },
  {
    path: '/tops',
    name: 'tops',
    component: () => import('../views/Tops.vue')
  },
  {
    path: '/products/:id',
    name: 'products',
    component: () => import('../views/ProductDetail.vue')
  },
  {
    path: '/users/:id',
    name: 'profile',
    component: () => import('../views/Profile.vue')
  },
  {
    path: '/users/:id/edit',
    name: 'editProfile',
    component: () => import('../views/ProfileEdit.vue')
  },
  {
    path: '/tops/users',
    name: 'topUsers',
    component: () => import('../views/TopUsers.vue')
  },
  {
    path: '/admin',
    exact: true,
    redirect: '/admin/products'
  },
  {
    path: '/admin/products',
    name: 'adminProducts',
    component: () => import('../views/AdminProducts.vue')
  },
  {
    path: '/admin/products/create',
    name: 'adminProductsCreate',
    component: () => import('../views/AdminProductsCreate.vue')
  },
  {
    path: '/admin/products/:id/edit',
    name: 'adminProductsEdit',
    component: () => import('../views/AdminProductsEdit.vue')
  },
  {
    path: '/admin/categories',
    name: 'adminCategories',
    component: () => import('../views/AdminCategories.vue')
  },
  {
    path: '/admin/users',
    name: 'adminUsers',
    component: () => import('../views/AdminUsers.vue')
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFound
  }
]

const router = new VueRouter({
  linkExactActiveClass: 'active',
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

router.beforeEach(async (to, from, next) => {
  try {
    // 取得瀏覽器中的token
    const tokenInLocal = window.localStorage.getItem('token')

    // 取得state 中是否已經有設定token
    const tokenInStore = store.state.token

    // 是否是已驗證的狀態
    let isAuthenticated = store.state.isAuthenticated

    // 有token的情況 並且 token 被修改 才向後端重複驗證
    // 沒有的話直接前往登入頁
    if (tokenInLocal && tokenInLocal !== tokenInStore) {
      // 重新做驗證
      isAuthenticated = await store.dispatch('fetchCurrentUser')
    }

    // 判斷 state 中的 category 是否已取得 如果取得 就不再重複發起請求
    let categoriesLength = store.state.categories.length
    if (categoriesLength === 0) {
      categoriesLength = await store.dispatch('fetchCategories')
    }

    // 定義目標的路由
    const pathWithoutAuthentication = ['sign-in', 'sign-up']

    // 未通過驗證 並且 目前不待在目標路由
    if (!isAuthenticated && !pathWithoutAuthentication.includes(to.name)) {
      // 強制導向登入頁
      next('/signin')
      return
    }

    // 通過驗證 並且 欲前往目標路由
    if (isAuthenticated && pathWithoutAuthentication.includes(to.name)) {
      // 強制導向首頁以外的頁面
      next('/')
      return
    }
    next()
  } catch (error) {
    console.log(error)
  }
})

export default router
