import Vue from 'vue'
import Vuex from 'vuex'
import usersAPI from '../apis/users'
import productsAPI from '../apis/products'

Vue.use(Vuex)

export default new Vuex.Store({
  // data
  state: {
    currentUser: {
      id: -1,
      name: '',
      avatar: '',
      email: '',
      isAdmin: false
    },
    isLoading: false,
    isDark: true,
    categories: [],
    isAuthenticated: false,
    token: ''
  },
  // modify status method
  mutations: {
    setCurrentUser (state, currentUser) {
      state.currentUser = {
        ...state.currentUser,
        ...currentUser
      }
      state.isLoading = true
      state.isAuthenticated = true
      state.token = window.localStorage.getItem('token')
    },
    setCategories (state, categories) {
      state.categories = [
        ...categories
      ]
    },
    revokeAuthentication (state) {
      state.currentUser = {}
      state.isAuthenticated = false
      window.localStorage.removeItem('token')
    },
    toggleGlobalTheme (state, setting) {
      state.isDark = setting
    }
  },
  actions: {
    // 拉取API 並設定state
    async fetchCurrentUser ({ commit }) {
      try {
        const { data } = await usersAPI.getCurrentUser()
        if (data.status === 'error') {
          throw new Error(data.message)
        }
        const { id, name, email, avatar, isAdmin } = data.currentUser
        commit('setCurrentUser', { id, name, email, avatar, isAdmin })
        return true
      } catch (err) {
        console.log(err.message)
        commit('revokeAuthentication')
      }
    },
    async fetchCategories ({ commit }) {
      try {
        const { data } = await productsAPI.categories.get()
        if (data.status === 'error') {
          throw new Error(data.message)
        }
        commit('setCategories', data.categories)
        return true
      } catch (err) {
        console.log(err.message)
      }
    }
  },
  // package state
  modules: {
  }
})
