import { apiHelper } from '../utils/helpers'

export default {
  getIndex ({ page, categoryId }) {
    const searchParams = new URLSearchParams({ page, categoryId })
    return apiHelper.get(`/admin/products?${searchParams.toString()}`)
  },
  getTops () {
    return apiHelper.get('/admin/products/top')
  },
  getFeeds () {
    return apiHelper.get('/admin/products/feeds')
  },
  categories: {
    get () {
      return apiHelper.get('/admin/categories')
    },
    create ({ name }) {
      return apiHelper.post('/admin/categories', { name })
    },
    update ({ id, name }) {
      return apiHelper.put(`/admin/categories/${id}`, { name })
    },
    delete ({ id }) {
      return apiHelper.delete(`/admin/categories/${id}`)
    }
  },
  products: {
    get () {
      return apiHelper.get('/admin/products/all')
    },
    getDetail ({ id }) {
      return apiHelper.get(`/admin/products/${id}`)
    },
    create ({ formData }) {
      return apiHelper.post('/admin/products', formData)
    },
    update ({ id, formData }) {
      return apiHelper.put(`/admin/products/${id}`, formData)
    },
    delete ({ id }) {
      return apiHelper.delete(`/admin/products/${id}`)
    }
  },
  comments: {
    create ({ payload }) {
      return apiHelper.post('/admin/comments', payload)
    },
    delete ({ id }) {
      return apiHelper.delete(`/admin/comments/${id}`)
    }
  }
}
