import Vue from 'vue'
import App from './App.vue'
import router from './router'

import VueDarkMode from '@growthbunker/vuedarkmode'
import NProgress from 'vue-nprogress-loading-bar'
import Carousel3d from 'vue-carousel-3d'
import store from './store'
import VueIziToast from 'vue-izitoast'

import 'izitoast/dist/css/iziToast.min.css'

Vue.use(Carousel3d)
const options = {
  latencyThreshold: 800, // Number of ms before progressbar starts showing
  router: true, // Show progressbar when navigating routes
  http: false // Show progressbar when doing Axios.http or Vue.http
}

Vue.use(VueIziToast)

Vue.use(NProgress, options, router)

Vue.use(VueDarkMode, {
  theme: 'dark',
  components: [
    'alert',
    'avatar',
    'badge',
    'button',
    'divider',
    'heading',
    'icon',
    'progress-bar',
    'spinner',
    'checkbox',
    'file',
    'image-uploader',
    'input',
    'input-numeric',
    'label',
    'message',
    'radios',
    'select',
    'tabs',
    'textarea',
    'toggle',
  ],
})

Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app')
