<template>
  <div class="container">

    <div class="row justify-content-center">
      <div class="card col-lg-5 p-5 shadow mt-5 signin__board">

        <div class="row logo flex justify-content-center mt-2">
          <div class="col-lg-8">
            <img src="images/prism_logo_color_mint.svg">
          </div>

          <div class="col-lg-12 text-center mb-3 font-weight-normal mt-2">
            Lifestyle online shop
          </div>
        </div>

        <form class="w-100" @submit.prevent.stop="handleSubmit">

          <div class="form-label-group mb-2">
            <label for="email">Email</label>
            <input
              id="email"
              name="email"
              type="email"
              class="form-control"
              placeholder="email"
              autocomplete="username"
              required
              autofocus
              v-model="email"
            />
          </div>

          <div class="form-label-group mb-3">
            <label for="password">Password</label>
            <input
              id="password"
              name="password"
              type="password"
              class="form-control"
              placeholder="Password"
              autocomplete="current-password"
              required
              v-model="password"
            />
          </div>

          <gb-button
            type="submit"
            full-width
            class="mt-3 o-guide__button gb-base-button gb-base-button--turquoise gb-base-button--dark"
            :disabled="isProcessing"
          >
            {{ isProcessing ? '處理中...' : '送出' }}
          </gb-button>

          <div class="text-center mb-3 mt-3">
            <router-link to="/signup">
              Sign Up
            </router-link>
          </div>

          <p class="mt-5 mb-3 text-muted text-center">© 2020-2021</p>
        </form>

      </div>
    </div>
  </div>
</template>

<script>
import authorizationAPI from '../apis/authorization'
import { iziToast } from '../utils/helpers'

export default ({
  data() {
    return {
      email: '',
      password: '',
      isProcessing: false
    }
  },
  methods: {
    async handleSubmit() {
      try{
        if(!this.email || !this.password) return this.$toast.error(' ', 'Email or password doesn;t exist', iziToast.options.error)
        this.isProcessing = true
        const { data } = await authorizationAPI.signIn({
          email: this.email,
          password: this.password
        })
        if (data.status !== 'success') {
          this.$toast.error(' ', data.message, iziToast.options.error)
          throw new Error(data.message)
        }
        window.localStorage.setItem('token', data.token)
        this.$store.commit('setCurrentUser', data.user)
        this.$router.push('/index')
        this.$toast.show(' ', 'Signin success.', iziToast.options.success)

      } catch (err) {
        console.log('Signin error' ,err)
        this.isProcessing = false
        this.email = ''
        this.password = ''
        this.$toast.error(' ', 'Incorrect Email or password', iziToast.options.error)
      }
    }
  }
})
</script>
