<template>
  <nav v-show="totalPage.length >= 2" aria-label="Page navigation example">
    <ul class="pagination">

      <li
        :class="[
          'page-item',
          {
            disabled: currentPage === 1,
          },
        ]"
      >
        <router-link
          class="page-link"
          aria-label="Previous"
          :to="{
            name: 'results',
            query: {
              categoryId,
              page: prevPage,
            },
          }"
        >
          <span aria-hidden="true">&laquo;</span>
        </router-link>
      </li>

      <li
        v-for="page in totalPage"
        :key="page"
        :class="[
          'page-item',
          {
            active: currentPage === page,
          },
        ]"
      >
        <router-link
          class="page-link"
          :to="{
            name: 'results',
            query: {
              categoryId,
              page,
            },
          }"
        >
          {{ page }}
        </router-link>
      </li>

      <li
        :class="[
          'page-item',
          {
            disabled: currentPage === totalPage.length,
          },
        ]"
      >
        <router-link
          class="page-link"
          aria-label="Next"
          :to="{
            name: 'results',
            query: {
              categoryId,
              page: nextPage,
            }
          }"
        >
          <span aria-hidden="true">&raquo;</span>
        </router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    currentPage: {
      type: Number,
      default: 1,
    },
    totalPage: {
      type: Array,
      required: true,
    },
    prevPage: {
      type: Number,
      required: true,
    },
    nextPage: {
      type: Number,
      required: true,
    },
    categoryId: {
      type: [Number, String],
      default: '',
    },
  },
}
</script>
